.App {
  padding: 30px;
  display: flex;
  flex-direction: column;
  background: url("../../assets/img/bg.jpg");
  background-size: 100%;
}

.App > div {
  margin-right: 50px;
}

.active-filter {
  background-color: #d97f7f;
}

.is-done {
  opacity: 0.5;
  font-weight: 700;
  color: #7F77E0;
}

.error {
  border: 3px solid red;
  outline: none;
}